import {
  Select,
  Input,
  Alert
} from "@windmill/react-ui";
import React, { useEffect, useState } from "react";
import { Scrollbars } from "react-custom-scrollbars-2";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import { useTranslation } from "react-i18next";
import { FiX } from "react-icons/fi";
import useProductSubmit from "hooks/Editstaysbmit";
import InputAreaTwo from "components/form/InputAreaTwo";
import UploaderThree from "components/image-uploader/UploaderThree";
import Title from "components/form/Title";
import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import Uploader from "components/image-uploader/Uploader";
import DrawerButton from "components/form/DrawerButtonEdit";
import {
  getAllAmminitiesList,
  getAllCommonProperyList,
} from "services/Api";
const ProductDrawer = ({ id, getAllStayList,pagenumber, SingleStay }) => {
  const { t } = useTranslation();
  const [loading, setLoading] = useState(true);
  const [SelectedValue, setSelectedValue] = useState("");
  const {
    register,
    onSubmit,
    errors,
    openModal,
    imageUrl,
    setImageUrl,
    handleSubmit,
    onCloseModal,
    setpropertyList,
    propertyList,
    isSubmitting,
    tapValue,
    setValue,
    setamminitiesList,
    amminitiesList,
    handleSelectLanguage,
    handleSelectImage,
  } = useProductSubmit(id, getAllStayList, SingleStay,pagenumber);
  const [selectedOption, setSelectedOption] = useState([
    { id: SingleStay?.Property?.id, name: SingleStay?.Property?.name },
  ]);

  useEffect(() => {
    console.log(
      SingleStay?.Stay_room_aminities?.map((amm) => amm?.Stay_aminity?.id),
      "SingleStay1"
    );

    getPropertyList();
    getAminitiesList();
    setImageUrl(SingleStay?.Stay_images || []);
    setSelectedOption([
      { id: SingleStay?.Property?.id, name: SingleStay?.Property?.name },
    ]);
    setValue("PropertyId", SingleStay?.Property?.id);
    setValue("name", SingleStay?.name);
    setValue("Max_guest_capacity", SingleStay?.maximum_guest_capacity);
    setValue("Number_of_sharing", SingleStay?.no_of_sharing);
    setValue("slots_availability", SingleStay?.slots_availability);
    setValue(
      "Aminities",
      SingleStay?.Stay_room_aminities?.map((amm) =>
        amm?.Stay_aminity?.id?.toString()
      )
    );
  }, [SingleStay]);

  useEffect(() => {
    console.log(selectedOption, "SingleStay");
  }, [selectedOption]);

  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    console.log(
      PropertyList?.data?.data?.propertyList,
      "getAllcommonProperyList"
    );
    setpropertyList(PropertyList?.data?.data?.propertyList);
    setLoading(false);
  };
  const getAminitiesList = async () => {
    const AmminitiesList = await getAllAmminitiesList();
    console.log(AmminitiesList.data.data.stayAmenities, "ammnitiesList");
    setamminitiesList(AmminitiesList?.data?.data?.stayAmenities);
    setLoading(false);
  };

  // const getAllStayList = async () => {
  //   const stayList = await getAllStaysList();
  //   console.log(stayList.data.data.stays,"stayList")
  //   setSatyList(stayList?.data?.data?.stays)
  //   setLoading(false)
  // };
  const containsSpecialCharsOrNumbers = (value) => {
    return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?0-9]/.test(value);
  };
  return (
    <>
      <Modal
        open={openModal}
        onClose={onCloseModal}
        center
        closeIcon={
          <div className="absolute top-0 right-0 text-red-500 focus:outline-none active:outline-none text-xl border-0">
            <FiX className="text-3xl" />
          </div>
        }
      >
        <div className="cursor-pointer">
          <UploaderThree
            imageUrl={imageUrl}
            setImageUrl={setImageUrl}
            handleSelectImage={handleSelectImage}
          />
        </div>
      </Modal>

      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Stay")}
            description={t(
              "Update your Stay and necessary information from here"
            )}
          />
        ) : (
          <Title
            register={register}
            handleSelectLanguage={handleSelectLanguage}
            title={t("Edit Stay")}
            description={t(
              "Edit your Stay and necessary information from here"
            )}
          />
        )}
      </div>

      <Scrollbars className="track-horizontal thumb-horizontal w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
          {tapValue === "Basic Info" && (
            <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Property")} />
                <div className="col-span-8 sm:col-span-4">
                  <Select
                    {...register(`PropertyId`, {
                      required: "Select Property is required",
                    })}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                    // defaultValue={SingleStay?.Property?.id || ""}
                  >
                    {/* <option value={SingleStay?.Property?.id} disabled hidden>
                      {t(SingleStay?.Property?.name)}
                    </option> */}
                    {propertyList?.map((pro) => (
                      <option value={pro.id}>{pro.name}</option>
                    ))}
                  </Select>
                  <Error errorName={errors.PropertyId} pos />
                </div>
              </div>

              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Name")} />
                <div className="col-span-8 sm:col-span-4">
                  <Input
                     {...register(`name`, {
                      required: "name required!",
                      validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                    })}
                    //  defaultValue={SingleStay?.name}
                    label={t("name")}
                    name="name"
                    type="text"
                    placeholder={t("Enter Name")}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  />
                  <Error errorName={errors.name} pos />
                </div>
              </div>
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Stay Type")} />
                <div className="col-span-8 sm:col-span-4">
                  <Select
                    {...register(`Tent`, {
                      required: "Select Stay is required",
                    })}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  >
                    <option value={SingleStay?.type} disabled hidden>
                      {SingleStay?.type}
                    </option>
                    <option value="tent">Tent</option>
                     <option value="hut">Hut</option>
                     <option value="cottage">Cottage</option>
                     <option value="dormitory">Dormitory</option>
                     <option value="room">Room</option>

                  </Select>
                  <Error errorName={errors.typeIdProof} pos />
                </div>
              </div>

              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Number Of Sharing")} />
                <div className="col-span-8 sm:col-span-4">
                  <Input
                   {...register('Number_of_sharing', {
                    required: 'Number Of Sharing required!',
                    pattern: {
                      value: /^[0-9]*$/,
                      message: 'Please enter Number Of Sharing valid number.',
                    },
                  })}
                    label={t("Number Of Sharing")}
                    name="Number_of_sharing"
                    type="text"
                    placeholder={t("Number Of Sharing")}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"

                  />
                  <Error errorName={errors.Number_of_sharing} pos />
                </div>
              </div>
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Max Guest Capacity")} />
                <div className="col-span-8 sm:col-span-4">
                  <Input
                   {...register('Max_guest_capacity', {
                    required: 'Max Guest Capacity required!',
                    pattern: {
                      value: /^[0-9]*$/,
                      message: 'Please enter Max Guest Capacity valid number.',
                    },
                  })}
                    label={t("Max Guest Capacity")}
                    name="Max_guest_capacity"
                    type="text"
                    placeholder={t("Max Guest Capacity")}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"

                  />
                  <Error errorName={errors.Max_guest_capacity} pos />
                </div>
              </div>
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Slot Availability")} />
                <div className="col-span-8 sm:col-span-4">
                   <Input
                            {...register("slots_availability", {
                              required: "Slot Availability required!",
                              pattern: {
                                value: /^[0-9]*$/,
                                message: "Please enter a valid number.",
                              },
                            })}
                            label={t("Total Availability")}
                            name="slots_availability"
                            type="text"
                            placeholder={t("Total Slot Availability")}
                            className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                          />
                    <Error errorName={errors.slots_availability} pos />
                  </div>
              </div>

              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Select Aminities")} />
                <div className="col-span-8 sm:col-span-4">
                  <Select
                    multiple
                    {...register(`Aminities`, {
                      required: "Select Aminities is required",
                    })}
                    className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  >
                    {/* {SingleStay?.Stay_room_aminities?.map((amm) => (
                      <option value={amm?.Stay_aminity?.id} selected>
                        {amm?.Stay_aminity?.name}
                      </option>
                    ))} */}

                    <option value="" defaultValue hidden>
                      {t("Select Aminities")}
                    </option>
                    {amminitiesList?.map((ammi, i) => (
                      <option value={ammi.id?.toString()}>{ammi.name}</option>
                    ))}
                  </Select>
                  <Error errorName={errors.typeIdProof} pos />
                </div>
              </div>
              <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                <LabelArea label={t("Photos")} />
                <div className="col-span-8 sm:col-span-4">
                  <Uploader
                    product
                    folder="product"
                    imageUrl={imageUrl}
                    setImageUrl={setImageUrl}
                  />
                  <Alert className="sm12" type="info"> Image should be 670540px and Image Size should be less than 5mb</Alert>
                </div>
              </div>
            </div>
          )}
          {/* <DrawerButton
              id={id}
              save
              title="Product"
              isSubmitting={isSubmitting}
              handleProductTap={handleProductTap}
            /> */}
          <DrawerButton imageUrl={imageUrl} setImageUrl={setImageUrl} onCloseModal={onCloseModal} id={id} title=" Stay" isSubmitting={isSubmitting} />
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(ProductDrawer);
