import {
    Avatar,
    Badge,
    TableBody,
    TableCell,
    TableRow,
  } from "@windmill/react-ui";
  import MainDrawer from "components/Properties/EditPropertiesModal";
  import ProductDrawer from "components/Stay/EditStay";
  import CheckBox from "components/form/CheckBox";
  import DeleteModal from "components/Stay/DeleteStay";
  import EditDeleteButton from "components/table/EditDeleteButton";
  import ShowHideButton from "components/table/ShowHideButton";
  import defalutimage from "../../assets/img/forgot-password-office-dark.jpeg"
  import Tooltip from "components/tooltip/Tooltip";
  import useToggleDrawer from "hooks/useToggleDrawer";
  import { t } from "i18next";
  import { useContext, useState } from "react";
  import { FiEdit, FiTrash2, FiZoomIn } from "react-icons/fi";
  import { Link } from "react-router-dom";
  import { showingTranslateValue } from "utils/translate";
  import { SidebarContext } from "context/SidebarContext";
import { getStayById } from "services/Api";
   
  //internal import
  
  const ProductTable = ({products,isCheck,getAllStayList, pagenumber,setIsCheck,wholeCount, lang}) => {
    const {toggleModal ,toggleDrawer1,toggleDrawer,setIsDrawerOpen1} =
    useContext(SidebarContext);
    const { title, setTitle, } = useState("");
    const [serviceId, setserviceId] = useState("")
    const [SingleStay, setSingleStay] = useState("")
    const handleUpdate = async(e) => {
      console.log("SingleStay",e)
      const singleview = await getStayById(e.id);
      if (singleview && singleview.status === 200) {
        console.log(singleview?.data?.data?.stay,"singleview")
        setSingleStay(singleview?.data?.data?.stay)
      }
      // setSingleStay(e)
      toggleDrawer1();
        }
        const handleModalOpen = (data) => {
          console.log(data,"DELETEsww","ww")
          setserviceId(data.id);
          setSingleStay(data)
          toggleModal();
          setTitle(title);
        };
  
    return (
      <>
        {isCheck?.length < 1 && <DeleteModal id={serviceId} getAllStayList={getAllStayList} title={title} SingleStay={SingleStay} />}
  
        <MainDrawer>
        <ProductDrawer pagenumber={pagenumber} isOpen={toggleDrawer1} getAllStayList={getAllStayList} onClose={toggleDrawer1}  SingleStay={SingleStay}/>
        </MainDrawer> 
  
        <TableBody>
          {products?.map((properties, i) => (
            <TableRow key={i + 1}>
              <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
              </TableCell>           
              <TableCell className="">
                <span className="text-sm ">{properties?.Property?.name}</span>
              </TableCell>
              <TableCell className="">
                <span className="text-sm">{properties?.name}</span>
              </TableCell>
              <TableCell className="">
                <span className="text-sm">{properties?.type}</span>
              </TableCell>
              <TableCell className="">
                <span className="text-sm">{properties?.no_of_sharing}</span>
              </TableCell>
              <TableCell className="">
                <span className="text-sm">{properties?.maximum_guest_capacity}</span>
              </TableCell>
              <TableCell className="">
              {properties.slots_availability > 1 ? (
                  <Badge type="success">{t(properties.slots_availability)}</Badge>
                ) : (
                  <Badge type="danger">{t(properties.slots_availability)}</Badge>
                )}
              </TableCell>
              <TableCell className="">
               <ul>{properties?.Stay_room_aminities?.map((am)=> ( <li className="text-sm">{am?.Stay_aminity?.name}</li>))}</ul>
              </TableCell>
              <TableCell className="">
              {properties?.Stay_images?.map((img) => (<img
              className="object-cover imgCustom"
              src={img.image}
              alt="Office"
            />))}
              </TableCell>
              {/* <TableCell className="text-center">
                <ShowHideButton id={properties._id} status={properties.status} />
              </TableCell> */}
              <TableCell>
                {/* <EditDeleteButton
                  id={properties._id}
                  product={properties}
                  isCheck={isCheck}
                  handleUpdate={handleUpdate}
                  handleModalOpen={handleModalOpen}
                  title={showingTranslateValue(properties?.title, lang)}
                /> */}
                <button
            onClick={() => {
              handleUpdate(properties)
            }}
            className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
          >
            <Tooltip
              id="edit"
              Icon={FiEdit}
              title={t("Edit")}
              bgColor="#10B981"
            />
          </button>
        <button
          onClick={() => handleModalOpen(properties)}
          className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
        >
          <Tooltip
            id="delete"
            Icon={FiTrash2}
            title={t("Delete")}
            bgColor="#EF4444"
          />
        </button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </>
    );
  };
  
  export default ProductTable;
  