import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Select,
  Input,
  Button,
  Card,
  CardBody,
  Pagination,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import UploadManyTwo from "components/common/UploadManyTwo";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import propertiesformlocal from "../../utils/StayList";
import ProductTable from "components/Stay/StayTableList";
import SelectCategory from "components/form/SelectCategory";
import MainDrawer from "components/drawer/MainDrawer";
import ProductDrawer from "components/Stay/AddStay";
import CheckBox from "components/form/CheckBox";
import useProductFilter from "hooks/useProductFilter";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import DeleteModal from "components/modal/DeleteModal";
import BulkActionDrawer from "components/drawer/BulkActionDrawer";
import TableLoading from "components/preloader/TableLoading";
import SettingServices from "services/SettingServices";
import { getAllStaysList } from "services/Api";

const Products = () => {
  const {
    title,
    allId,
    serviceId,
    product,
    handleDeleteMany,
    handleUpdateMany,
  } = useToggleDrawer();
  const { t } = useTranslation();
  const {
    toggleDrawer,
    lang,
    currentPage,
    searchText,
    category,
    setCategory,
    searchRef,
    handleSubmitForAll,
    sortedField,
    setSortedField,
    limitData,
  } = useContext(SidebarContext);

  // const { data, loading } = useAsync(() =>
  //   ProductServices.getAllProducts({
  //     page: currentPage,
  //     limit: limitData,
  //     category: category,
  //     title: searchText,
  //     price: sortedField,
  //   })
  // );

  const [loading, setLoading] = useState(true);
  const [isCheckAll, setIsCheckAll] = useState(false);
  const [isCheck, setIsCheck] = useState([]);
  const [pagenumber, setpagenumber] = useState(1);
  const [AllStayDetailList, setAllStayDetailList] = useState([]);
  const [wholeCount, setwholeCount] = useState({});
  const handleSelectAll = () => {
    setIsCheckAll(!isCheckAll);
    setIsCheck(propertiesformlocal.map((li) => li._id));
    if (isCheckAll) {
      setIsCheck([]);
    }
  };

  useEffect(() => {
    getAllStayList(pagenumber);
    console.log(product, serviceId, "dataaaaa");
  }, [serviceId]);

  console.log("productss", propertiesformlocal);
  const { serviceData } = useProductFilter(propertiesformlocal);

  const getAllStayList = async (pagenumber, search = "") => {
    const staylist = await getAllStaysList(pagenumber, search);
    console.log(staylist.data.data, "staylist");
    setLoading(false);
    setAllStayDetailList(staylist?.data?.data);
    setwholeCount(staylist?.data?.data?.pagination);
  };
  const handleChangePage = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllStayList(e);
  };

  const handleSearch = (search) => {
    getAllStayList(pagenumber, search);
  };
  return (
    <>
      <PageTitle>{t("Stay Details")}</PageTitle>
      <MainDrawer>
        <ProductDrawer id={serviceId} getAllStayList={getAllStayList} />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody className="">
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-4 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <Input
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="lg:flex  md:flex xl:justify-end xl:w-1/2  md:w-full md:justify-start flex-grow-0">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Button
                  onClick={toggleDrawer}
                  className="w-full rounded-md h-12"
                >
                  <span className="mr-2">
                    <FiPlus />
                  </span>
                  {t("Add Stay")}
                </Button>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{"s.no"}</TableCell>
                <TableCell>{t("Porperty Name")}</TableCell>
                <TableCell>{t("Stay Name")}</TableCell>
                <TableCell>{t("Stay Type")}</TableCell>
                <TableCell>{t("Sharing")}</TableCell>
                <TableCell className="">{t("capacity")}</TableCell>
                <TableCell className="">{t("Total Availability")}</TableCell>
                <TableCell className="">{t("Aminities")}</TableCell>
                <TableCell className="text-center" width={"350px"}>
                  {t("Photos")}
                </TableCell>
                {/* <TableCell className="text-center">{t("Status")}</TableCell> */}
                {
                  <TableCell className="text-right">{t("Action")}</TableCell>
                  /*  <TableCell className="text-center">
                  {t("PublishedTbl")}
                </TableCell>
                <TableCell className="text-right">{t("ActionsTbl")}</TableCell> */
                }
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              isCheck={isCheck}
              pagenumber={pagenumber}
              getAllStayList={getAllStayList}
              products={AllStayDetailList?.stays}
              setIsCheck={setIsCheck}
              wholeCount={wholeCount}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={AllStayDetailList?.pagination?.total}
              resultsPerPage={AllStayDetailList?.pagination?.pageSize}
              onChange={handleChangePage}
              label="Stay Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
